.chat {
  /* margin: 5px auto; */
  /* width: 60vw; */
  height: 90vh;
  border: 1px solid #d8d8fd;
  border-radius: 4px;
  box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.message-list {
  flex: 10 1;
  overflow-y: scroll;
}
.messages {
  display: flex;
  flex-direction: column-reverse;
  min-height: 100%;
  padding: 0 10px;
}
